/* **************** CARD ACTIONS ****************** */
.card-actions {
  border-radius: 0; }
  .card-actions .switch-item {
    border-bottom: 2px solid #7792af;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0; }
  .card-actions .switch-item__txt {
    color: #626262;
    display: inline-block;
    font-size: 14px;
    padding-left: 20px; }
  .card-actions .switch {
    display: inline-block; }
    .card-actions .switch > label .lever {
      background-color: #eee;
      height: 25px;
      width: 39px; }
      .card-actions .switch > label .lever::before, .card-actions .switch > label .lever::after {
        margin-left: 1px;
        top: 2px; }
    .card-actions .switch > label input[type="checkbox"]:checked + .lever::before, .card-actions .switch > label input[type="checkbox"]:checked + .lever::after {
      margin-left: -1px; }

.range-slider-mono,
.range-slider-dual {
  margin: 25px 20px; }
  .range-slider-mono .noUi-tooltip span,
  .range-slider-dual .noUi-tooltip span {
    top: 8px;
    left: 0; }
  .range-slider-mono.noUi-target.noUi-horizontal .noUi-tooltip,
  .range-slider-dual.noUi-target.noUi-horizontal .noUi-tooltip {
    background-color: #fff;
    border: 2px solid #eee;
    height: 32px;
    width: 32px;
    top: -19px;
    left: -3px; }
  .range-slider-mono.noUi-target.noUi-horizontal .noUi-handle.noUi-active .noUi-tooltip,
  .range-slider-dual.noUi-target.noUi-horizontal .noUi-handle.noUi-active .noUi-tooltip {
    background-color: #26a69a; }
